  padding-top: 50px;
 
}

nav ul, .contact ul {
  list-style: none;
 }

 nav{
  width: 100%;
  padding-bottom: 100px;
  color: white;

 }

nav li {
  width:100%;
}

nav a {
  position:  center;
  width: 100%;
  height: 30px;
  padding: 10px 0;
  line-height: 30px;
}

nav li:nth-child(2n+1) a:after {
  position: absolute;
  content: '';
  top: 19.5px;
  right: -2.5px;
  z-index: 10;
  border-radius: 100%;
  background: rgb(35, 76, 109);
  width: 5px;
  height: 5px;
}


nav li {
    width: auto;
    float: right;
    padding-top: 50px;
    padding-bottom: 50px;

  }

  a {
    box-shadow: white;
    color: white;
    
  }

  nav a {
    padding: 7px;
    border-right: 10px 1px  ;
    float: right;
    width: 100px;
    font-size: 18px;
   }

  

