.projectBox {
  background-size: cover;
  width: 100%;
  background:  no-repeat center;
  margin: 0;
  position: relative;
  display: inline-block;
  border-radius: 10%;
}

.projectName {
  color: white;
  border: white 3px solid ;
  text-align: center; 
  display: inline-block;
  padding: 5px 20px;
  background: rgba(255,255,255,0.4);
  font-size: 20pt;
  font-family: "Garamond", sans-serif;
  position: relative;
  top: 35%;
 }

.projectsBox {
  -webkit-transition: ease-in-out 0.8s;
  transition: ease-in-out 0.8s;
  position: relative;
  margin: 0;
  width: 80;
   
}

.projectBox {
  -webkit-transition: ease-in-out 0.3s;
  transition: ease-in-out 0.3s;
  background-size: contain;
  background-color: darkslategray;
}

.projectBox:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  opacity: 1 !important;
}

.projectsBox {
  pointer-events: none;
}

.projectsBox .projectBox {
  pointer-events: auto;
}

.projectsBox:hover .projectBox {
  opacity: 0.5;
}

.projectsBox:hover .projectBox:hover {
  opacity: 1;
}
 

.description{ 
  margin-top: 25px;
  margin-bottom: 50px ;


}
.bubble {
	/*background-color: #00b4ff;*/
	width: 200px;
	height: 200px;
	border-radius: 50%;
	z-index: 100;
    margin-left: -100px;
    margin-top: -100px;
	position: "absolute";
	box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2), inset 0px 10px 30px 5px rgba(255, 255, 255, 1);
    /*background: radial-gradient(ellipse at center, rgba(0,180,255, 0) 0%,rgba(255,255,255,0.5) 70%);*/
    pointer-events: none;
}

.bubble:after {
	background: radial-gradient(ellipse at center, rgba(0,180,255,0.45) 0%,rgba(255,255,255,0) 70%);
    /*background: radial-gradient(ellipse at center,  rgba(255,255,255,0.5) 0%,rgba(255,255,255,0) 70%);*/

	box-shadow: inset 0 20px 30px rgba(255, 255, 255, 0.3);
    border-radius: 50%;
    content: "";
    height: 180px;
    left: 10px;
    position: absolute;
    width: 180px;
}

